		playvideo: function(path) {
			// this.poster = poster;
			var videoContext = this.videoContext;
			this.videoSrc = path;
			setTimeout(() => {
				videoContext.seek(0);
				videoContext.play();
				this.isShowVideo = true;
				videoContext.requestFullScreen();
			}, 150);
			// setTimeout(()=>{
			// 	videoContext.requestFullScreen();
			// },150)

		},
		pause(e) {
			console.log('结束')
			var videoContext = this.videoContext;
			videoContext.exitFullScreen();
			// this.videoSrc = '';
			this.isShowVideo = false;
		},
		/**
		 * 全屏改变
		 */
		bindVideoScreenChange: function(e) {
			var status = e.mp.detail.fullScreen;
			console.log(status);
			if (!status) {
				var videoContext = this.videoContext;
				videoContext.seek(0);
				videoContext.pause();
				// this.videoSrc = '';
				this.isShowVideo = false;
			} else {
				// this.isShowVideo = true;
			}
			//this.setData(play);
